@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body{
  text-align: center;
  background: #c31432;
  background: linear-gradient(to right, #240b36, #c31432);
  font-family: poppins;
}
.jumbotron{
    background: none;
    background-color: #29292975;
    padding: 30px;
    border-bottom: 5px solid #ff000048;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
}
.box {
  padding: 30px 40px;
  border-radius: 15px;
  background-color: rgba(75, 100, 114, 0.24);
}
.box:hover {
  -webkit-transform: scale(1.07);
          transform: scale(1.07); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
  background-color: rgba(47, 56, 63, 0.315);
}
.combo{
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  background-color: #ffffff8e;
  border-radius: 8px;
  margin-bottom: 10px;
}
h1, h2{
  color: rgb(206, 206, 206)
}
h3{
  color: #ec5f1d;
}
p{
  line-height: 35px;
  font-size: 21.7px;
  color: rgb(206, 206, 206);
}
p:hover{
  color: #ffffffee;
}
a:hover{
  text-decoration: none;
  color: white;
}
footer{
  background-color: #240b366e;
}

  
.lastupdate{
  font-size: 0.7em;
  color: beige;
}
.btnHome{
  left: 10px; 
  width: 135px;
  top: 10px;
  height: 43px;
  line-height: normal;
  position: absolute;
  text-decoration: none;
  font-size: 13px;
  border-radius: 25px;
  background-color: rgba(0, 0, 0, 0.178);
  border: none;
  color: white;
}
.btnHome:hover{
  background-color: rgba(0, 0, 0, 0.404);
}

